<template>
    <div class="change-num">
      <div @click="minus" class="minus">-</div><div>{{num}}</div><div @click="add" class="add">+</div>
    </div>
</template>

<script>
  export default {
    name: 'change-num',
    props:{
      num:{
        type:Number,
        default:1
      }
    },
    methods:{
      minus(){
        this.$emit('minus')
      },
      add(){
        this.$emit('add')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .change-num
    width 6.626rem
    height 1.875rem
    display flex
    justify-content space-around
    div
      flex 1
      line-height 1.875rem
      text-align center
      font-size 0.938rem
      font-weight bold
      color #333
      background-color #f5f5f5
    .minus
      color #999
      border-right 1px solid #fff
    .add
      color #999
      border-left 1px solid #fff
</style>



