<template>
    <div class="commodity-des">
      <div class="des-wrapper">
        <div class="des-info">
          <p>商品详情</p>
          <div v-if="data" v-html="data.content">
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'commodity-des',
    props:{
      data:{
        type:Object,
        default:null
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .commodity-des
    padding-top 0.375rem
    background-color #f5f5f5
    .des-wrapper
      background-color #fff
      padding-bottom 1rem
      .des-info
        width 91.47%
        margin 0 auto
        p
          line-height 2.906rem
          font-size 1.125rem
          color #333
          font-weight bold

</style>



