<template>
  <div v-if="data" class="return-money">
      <div class="return-info">
        <div class="return-main">
          <div class="return-top">
            <span class="tip">返现</span>
            <p class="des">最多可返<span>{{data.maxReturnTb}}</span>通宝</p>
          </div>
          <div class="return-bottom">返还规则：为实付现金的{{data.returnTbRate}}%</div>
        </div>
      </div>
  </div>
</template>

<script>
  export default {
    name: 'return-money',
    props:{
      data:{
        type:Object,
        default:null
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.return-money
  width 100%
  background-color #f5f5f5
  padding-top 0.625rem
  .return-info
    background-color #fff
    .return-main
      width 91.47%
      padding 0.75rem 0
      margin 0 auto
      .return-top
        font-size 0.813rem
        color #333
        .tip
          font-size 0.688rem
          display inline-block
          width 1.875rem
          height 1rem
          line-height 1rem
          text-align center
          border-radius 0.125rem
          background-color #ff4450
          color #fff
        .des
          display inline-block
          span
            color #ff4800
      .return-bottom
        padding-top 0.625rem
        font-size 0.75rem
        color #999
</style>



