import CONFIG from './config'
import axios from 'axios'
import qs from 'qs'

/*
* 获取商品详情
* */
export function commodityDetails(opts) {
  return axios.post(CONFIG.API.COMMODITY_DETAILS, qs.stringify({
    itemId:opts.itemId
  })).then(res => res.data)
}

/*
* 获取商品sku
* */
export function getSku(opts) {
  return axios.post(CONFIG.API.GET_SKU, qs.stringify({
    specVals:opts.specVals
  })).then(res => res.data)
}
/*
* 获取商品sku
* */
export function jionCar(opts) {
  return axios.post(CONFIG.API.JION_CAR, qs.stringify({
    sku:opts.sku,
    num:opts.num
  })).then(res => res.data)
}

/*
* navlist
* */
export function shopCount() {
  return axios.post(CONFIG.API.SHOP_COUNT).then(res => res.data)
}



