<template>
  <transition name="fade">
    <div class="change">
      <div class="change-wrapper">
        <div class="change-title">
          <img :src="imgUrl" alt="">
          <i @click="close" class="iconfont close">&#xe63f;</i>
          <p class="price">{{priceTxt(price.minPayTb,price.cashPrice)}}</p>
          <p class="kind">{{change}}</p>
        </div>
        <scroll ref="changeScroll" :data="data" class="change-main">
          <div>
            <div v-for="(item,index) in data" :key="item.id"
                 class="item-wrapper">
              <div class="title">{{item.specName}}</div>
              <div class="title-kind">
                <span @click="handleKind(i.specValue,index,i.id)"
                      :class="i.specValue===item.change?'active':''"
                      v-for="i in item.specValList">{{i.specValue}}</span>
              </div>
            </div>
            <div class="change-num-wrapper">
              <span>购买数量</span>
              <change-num @minus="minus" @add="add"  :num="num"></change-num>
            </div>
          </div>
        </scroll>
        <div class="change-btn">
          <div v-show="type===2||type===3" class="confirm" @click="confirm(0)">确定</div>
          <div v-show="type===1" class="two-wrapper">
            <div class="one" @click="confirm(2)">加入购物车</div>
            <div class="two" @click="confirm(3)">立即购买</div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import Scroll from '../../base/scroll/scroll'
  import tool from '../../common/js/util'
  import ChangeNum from '../../base/change-num/change-num'

  export default {
    name: 'change',
    props: {
      price:{
        type:Object,
        default:null
      },
      imgUrl:{
        type:String,
        default:''
      },
      type: {
        type: Number,
        default: 1
      },
      data: {
        type: Array,
        default: function () {
         return []
        }
      },
      change:{
        type:String,
        default:''
      },
      num:{
        type:Number,
        default:1
      }
    },
    components: {
      Scroll,
      ChangeNum
    },
    methods: {
      priceTxt(tb,rmb){
        return tool.priceStr(tb,rmb)
      },
      confirm(e){
        this.$emit('confirm',e)
      },
      handleKind(a,b,c) {
        this.$emit('handleKind',{a,b,c})
        this.$forceUpdate()
      },
      refresh(){
        this.$refs.changeScroll.refresh()
      },
      close() {
        this.$emit('close')
      },
      minus(){
        this.$emit('minus')
      },
      add(){
        this.$emit('add')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  @import "../../common/stylus/mixin.styl"
  .change
    position fixed
    top 0
    left 0
    right 0
    bottom 0
    z-index 999
    background-color rgba(0, 0, 0, 0.65)
    .change-wrapper
      width 100%
      height 76.76%
      position absolute
      bottom 0
      background-color #fff
      border-radius 0.469rem 0.469rem 0 0
      padding 0.938rem
      box-sizing border-box
      .change-title
        .price
          display inline-block
          position absolute
          bottom 1.594rem
          font-size 0.938rem
          color #ff4800
        .kind
          position absolute
          font-size 0.813rem
          color #999
          bottom 0
          display inline-block
        img
          position absolute
          height 6.875rem
          width 6.875rem
          left 0
        width 100%
        padding-left 7.625rem
        height 22.73%
        box-sizing border-box
        position relative
        .close
          extend-click()
          position absolute
          top 0
          right 0
      .change-main
        width 100%
        overflow hidden
        height 60%
        margin-top 1rem
        box-sizing border-box
        .item-wrapper
          padding-bottom 1rem
          color #333
          .title
            font-size 0.938rem
            line-height 2.25rem
          span
            margin-bottom 0.5rem
            font-size 0.875rem
            display inline-block
            margin-right 0.5rem
            border-radius 0.313rem
            padding 0.719rem 0.469rem
            background-color #f7f7f7
            border 1px solid #f7f7f7
          .active
            background-color #fff9f7
            border 1px solid #ff4800
        .change-num-wrapper
          display flex
          align-items center
          justify-content space-between
          span
            font-size 0.938rem
            color #333

      .change-btn
        width 100%
        height 3.063rem
        overflow hidden
        border-radius 1.6rem
        margin-top 1rem
        color #fff
        background-color #ff4800
        .confirm
          line-height 3.063rem
          text-align center
        .two-wrapper
          display flex
          height 100%
          justify-content space-between
          .one, .two
            height 100%
            width 50%
            line-height 3.063rem
            text-align center
            background-color #ffa800
          .two
            background-color #ff4800

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
  {
    opacity: 0;
  }
</style>



