<template>
<div v-if="data" class="commodity-details-title">
    <div class="price-wrapper">
      <div class="present-price">
        <p class="num">{{priceTxt(data.minPayTb,data.cashPrice)}}</p>
        <p class="txt">支持通宝抵扣</p>
        <p class="txt">包邮</p>
      </div>
      <div class="original-price">
        市场价：￥{{data.oldPrice}}
      </div>
    </div>
    <div class="commodity-name">
      <div class="commodity-title">
        {{data.title}}
      </div>
      <div class="commodity-des">
        {{data.subTitle}}
      </div>
    </div>
</div>
</template>

<script>
  import tool from '../../common/js/util'
  export default {
    name: 'commodity-des-details-title',
    props:{
      data:{
        type:Object,
        default:null
      }
    },
    methods:{
      priceTxt(tb,rmb){
        return tool.priceStr(tb,rmb)
      },
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .commodity-details-title
    width 91.47%
    margin 0 auto
    .price-wrapper
      padding 0.313rem 0 0.938rem 0
      border-bottom 1px solid #eaeaea
      .present-price
        line-height 2.313rem
        color #ff4800
        font-size 1.125rem
        line-height
        p
          display inline-block
          vertical-align middle
        .txt
          line-height 1
          font-size 0.688rem
          padding 0.313rem 0.719rem
          border-radius 0.625rem
          background-color rgba(255,72,0,0.1)
      .original-price
        font-size 0.75rem
        color #999
        text-decoration line-through
    .commodity-name
      padding 0.313rem 0
      .commodity-title
        font-size 1.063rem
        font-weight bold
        color #333
        line-height 2.313rem
      .commodity-des
        font-size 0.875rem
        line-height 1.375rem
</style>



