<template>
  <div class="bottom-bar">
    <div class="bar-left">
      <div @click="toService" class="service">
        <i class="iconfont">&#xe629;</i>
        <p>联系客服</p>
      </div>
      <div @click="toShopCar" class="shopping-trolley">
        <i class="iconfont">&#xe63c;</i>
        <p>购物车</p>
        <div v-show="num<10" class="num">{{num}}</div>
        <div v-show="num>9" class="num1">{{num}}</div>
      </div>
    </div>
    <div class="bar-right">
      <div @click="add" class="add">
        加入购物车
      </div>
      <div @click="by" class="by">
        立即购买
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'bottom-bar',
    props:{
      num:{
        type:Number,
        default:0
      }
    },
    methods: {
      add() {
        this.$emit('add')
      },
      by() {
        this.$emit('by')
      },
      toService() {
        this.$emit('toService')
      },
      toShopCar() {
        this.$emit('toShopCar')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .bottom-bar
    position fixed
    bottom 0
    left 0
    width 100%
    height 3.063rem
    z-index 500
    display flex
    justify-content space-between
    border-top 1px solid #e6e6e6
    .bar-left
      width 40%
      display flex
      flex-wrap nowrap
      justify-content space-between
      text-align center
      padding 0.5rem 0
      font-size 0.625rem
      background-color #fff
      .shopping-trolley
       position relative
       .num, .num1
         font-size 0.75rem
         color #fff
         position absolute
         right 1.2rem
         min-width 1rem
         height 1rem
         border-radius 0.5rem
         text-align center
         line-height 1rem
         background-color #ff1111
         top -0.4rem
       .num1
         padding 0 3px
      .service, .shopping-trolley
        height 100%
        width 50%
        i
          display inline-block
          font-size 1.188rem
          padding-bottom 0.3125rem

    .bar-right
      width 60%
      display flex
      justify-content space-between
      .add, .by
        line-height 3.063rem
        text-align center
        height 100%
        width 50%
        color #fff
        background-color #ff4800
      .add
        background-color #ffa800
</style>



