<template>
  <div v-if="detail" class="commodity-details">
    <!--<shop-header style="z-index: 999;background-color: #fff"-->
    <!--title="商品详情"></shop-header>-->
    <scroll class="commodity-scroll" ref="commodityScroll">
      <div>
        <div v-if="swiperList.length" class="slider-wrapper"
             ref="sliderWrapper">
          <slider :loop="false" :auto-play="false">
            <div v-for="item in swiperList">
              <img class="needsclick" @load="loadImage" :src="item">
            </div>
          </slider>
        </div>
        <commodity-details-title :data="detail"></commodity-details-title>
        <return-money :data="detail"
                      v-if="detail.needReturnTb==='Y'"></return-money>
        <change-kind v-if="detail.specNum!==0" :kind-txt="kindTxt"
                     @change="change"></change-kind>
        <commodity-des id="article-box" :data="detail"></commodity-des>
      </div>
    </scroll>
    <bottom-bar :num="shopNum" @toShopCar="toShopCar" @toService="toService" @add="add"
                @by="by"></bottom-bar>
    <change :price="detail" :imgUrl="imgUrl" @confirm="confirm" ref="change"
            :num="num" @minus="minus"
            @add="addNum" :change="change1" @handleKind="handleKind"
            v-if="detail.specNum!==0" :data="specList" :type="type"
            @close="close" v-show="showChange"></change>
  </div>
</template>

<script>
  import tool from '../../common/js/util'
  import ShopHeader from '../../base/shop-header/shop-header.vue'
  import Scroll from '../../base/scroll/scroll'
  import Slider from '../../base/slider/slider'
  import ReturnMoney from '../../base/return-money/return-money'
  import CommodityDes from '../../base/commodity-des/commodity-des'
  import ChangeKind from '../../base/change-kind/change-kind'
  import BottomBar from '../../base/bottom-bar/bottom-bar'
  import Change from '../../base/change/change'
  import CommodityDetailsTitle
    from '../../base/commodity-details-title/commodity-details-title'
  import * as core from '../../api/commodityDetails'

  export default {
    name: 'commodity-des-details',
    data() {
      return {
        swiperList: [],
        detail: null,
        showChange: false,
        kindTxt: '请选择规格',
        specList: [],
        type: 1,
        change1: '',
        num: 1,
        imgUrl: '',
        shopNum:0
      }
    },
    components: {
      ShopHeader,
      Slider,
      Scroll,
      CommodityDetailsTitle,
      ReturnMoney,
      ChangeKind,
      CommodityDes,
      BottomBar,
      Change
    },
    created() {
    this.getShopCount()
      document.title = this.$route.meta.title     // console.log(this.$route.query.cartIds)
      this.getRecommendGoods({itemId: this.$route.query.commodityId})
    },
    methods: {
      getShopCount() {
        core.shopCount().then(res => {
          if (res.code && '00' === res.code) {
            this.shopNum = res.result.count
            console.log(res, 'dasdasdsadass')
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      toService() {
        window.location.href = 'https://ww16.53kf.com/m.php?cid=72187208&style=1&keyword=http%3A%2F%2Ftest.51jujibao.com%3A8080%2F&referer=http%3A%2F%2Fm.51jujibao.com%2Fv2%2Findex.html%23%2Fcenter&guest_id=10562525909006&tpl=crystal_blue&uid=1827a2a86496d5a9c9a1617ea6489f0b&u_stat_id=&talktitle=%E4%B8%AA%E4%BA%BA%E4%B8%AD%E5%BF%83&tfrom=50&device=&zdkf_type=1&kf=&kflist=off'
      },
      toShopCar() {
        this.$router.push({path: '/shopCar'})
      },
      getSku(opts) {
        core.getSku(opts).then(res => {
          if (res.code && '00' === res.code) {
            this.imgUrl = res.result.cover;
            this.sku = res.result.id,
            this.detail.minPayTb=res.result.minPayTb
            this.detail.cashPrice=res.result.cashPrice
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      joinShopCar(opts) {
        core.jionCar(opts).then(res => {
          if (res.code && '00' === res.code) {
            this.getShopCount()
            this.showChange = false
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      confirm(e) {
        if (e === 0) {
          if (this.type === 2) {
            if (this.idArr) {
              console.log(this.idArr);
              console.log(this.num);
              this.joinShopCar({sku: this.sku, num: this.num});
              console.log('加入购物车')
            }else {
              this.$toastBox.showToastBox('请选择规格')
            }
          } else if (this.type === 3) {
            if (this.idArr) {
              console.log(this.idArr);
              console.log(this.num);
              this.$router.push({
                path: '/purchaseNow',
                query: {sku: this.sku, num: this.num}
              })
              console.log('立即购买')
            }else {
              this.$toastBox.showToastBox('请选择规格')
            }
          }
        } else if (e === 2) {
          if (this.idArr) {
            console.log(this.idArr);
            console.log(this.num);
            this.joinShopCar({sku: this.sku, num: this.num});
            console.log('加入购物车')
          }else {
            this.$toastBox.showToastBox('请选择规格')
          }

        } else if (e === 3) {
          if (this.idArr) {
            console.log(this.idArr);
            console.log(this.num);
            this.$router.push({
              path: '/purchaseNow',
              query: {sku: this.sku, num: this.num}
            })
            console.log('立即购买')
          }else {
            this.$toastBox.showToastBox('请选择规格')
          }
        }
      },
      addNum() {
        this.num++
      },
      minus() {
        if (this.num <= 1) {
          this.$toastBox.showToastBox('购买数量不能小于1')
        } else {
          this.num--
        }
      },
      handleKind(e) {
        this.specList[e.b].change = e.a;
        this.specList[e.b].changeId = e.c;
        let tip = '请选择';
        let tip1 = '全选';
        let arr = []
        for (var i = 0; i < this.specList.length; i++) {
          if (this.specList[i].change === '') {
            tip = tip + ' ' + this.specList[i].specName
          } else {
            arr.push(this.specList[i].changeId)
            tip1 = tip1 + ' ' + this.specList[i].change;
          }
        }
        if (tip === '请选择') {
          this.idArr = arr;
          this.getSku({specVals: arr.join(',')})
          this.change1 = tip1;
          this.kindTxt = tip1
        } else {
          this.change1 = tip
        }
        // kindTxt: '请选择规格',
        // change1: ''

      },
      add() {
        this.type = 2;
        this.showChange = true
        setTimeout(() => {
          this.$refs.change.refresh()
        }, 20)
      },
      by() {
        this.type = 3;
        this.showChange = true
        setTimeout(() => {
          this.$refs.change.refresh()
        }, 20)
      },
      close() {
        this.showChange = false
      },
      change() {
        this.type = 1
        this.showChange = true
        setTimeout(() => {
          this.$refs.change.refresh()
        }, 20)
      },
      loadImage() {
        if (!this.checkloaded) {
          this.checkloaded = true;
          this.$refs.commodityScroll.refresh()
        }
      },
      getRecommendGoods(opts) {
        core.commodityDetails(opts).then(res => {
          if (res.code && '00' === res.code) {
            let data = res.result
            this.imgUrl = data.cover
            this.swiperList = data.sliderImgList
            this.detail = data
            let _this = this
            setTimeout(() => {
              let imgDom = document.getElementById('article-box').getElementsByTagName('img')
              if (imgDom.length > 0) {
                for (var i = 0; i < imgDom.length; i++) {
                  imgDom[i].onload = function () {
                    _this.$refs.commodityScroll.refresh();
                  }
                }
              }
            }, 20)
            this.specList = data.specList
            if (data.specNum > 0) {
              let tip = '请选择';
              for (var i = 0; i < this.specList.length; i++) {
                this.specList[i].change = '';
                this.specList[i].changeId = '';
                tip = tip + ' ' + this.specList[i].specName
              }
              this.change1 = tip
              console.log(this.change1)
            }
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .commodity-details
    .commodity-scroll
      position fixed
      /*top 2.75rem*/
      top 0
      bottom 3.036rem
      left 0
      right 0
      .slider-wrapper
        position: relative
        width: 100%
        overflow: hidden


</style>



