<template>
  <div class="change-kind">
    <div @click="change" class="change-wrapper">
      <div class="change-info">
        <div class="change">选择</div>
        <div class="kind">{{kindTxt}}</div>
        <div class="tip"><i class="iconfont">&#xe624;</i></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'change-kind',
    props: {
      kindTxt: {
        type: String,
        default: '请选择规格'
      }
    },
    methods: {
      change() {
        this.$emit('change')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .change-kind
    background-color #f5f5f5
    padding-top 0.625rem
    .change-wrapper
      background-color #fff
      .change-info
        width 91.47%
        margin 0 auto
        position relative
        div
          display inline-block
          font-size 0.875rem
          color #333
          line-height 2.75rem
        .change
          color #999
          padding-right 1rem
        .tip
          position absolute
          top 0
          right 0
          color #999

</style>



